import { _decorator, Component, Node, AudioSource, AudioClip, resources } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Audio')
export class Audio extends Component {
     //
     private static instance: Audio;
     //instance
     public static getInstance(): Audio {
         return this.instance;
     }
    @property({ type: AudioSource })
    audioSource: AudioSource = null;


    @property({ type: AudioClip })
    dlive: AudioClip = null;

    @property({ type: AudioClip })
    gameover: AudioClip = null;

    @property({ type: AudioClip })
    coll: AudioClip = null;

    @property({ type: AudioClip })
    clic: AudioClip = null;

    @property({ type: AudioClip })
    lover: AudioClip = null;

    @property({ type: AudioClip })
    lvic: AudioClip = null;
    private soundCache: Map<string, AudioClip> = new Map();

     protected start(): void {
        Audio.instance = this;
     }

     private isSoundEnabled: boolean = true; // 
    private isMusicEnabled: boolean = true; // 

     public playSound(soundPath: string) {
        if (!this.isSoundEnabled || !this.audioSource) return;

        // 
        if (this.soundCache.has(soundPath)) {
            this.audioSource.playOneShot(this.soundCache.get(soundPath)!);
            return;
        }

        // 
        resources.load(soundPath, AudioClip, (err, clip) => {
            if (err) {
                console.error(`: ${soundPath}`, err);
                return;
            }
            this.soundCache.set(soundPath, clip);
            this.audioSource!.playOneShot(clip);
        });
    }
    playDlive() {
        this.audioSource.volume = 0.3;
        this.audioSource.playOneShot(this.dlive);
    }

    playGameover() {
        this.audioSource.volume = 0.3;
        this.audioSource.playOneShot(this.gameover);
    }
    playCollider() {
        this.audioSource.volume = 0.3;
        this.audioSource.playOneShot(this.coll);
    }
    playClick() {
        this.audioSource.volume = 0.3;
        this.audioSource.playOneShot(this.clic);
    }
    playlevelover(){
        this.audioSource.volume = 0.3;
        this.audioSource.playOneShot(this.lover);
    }
    playlevelvic(){
        this.audioSource.volume = 0.3;
        this.audioSource.playOneShot(this.lvic);
    }
}


