import { _decorator, Component, Label, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('GameoverUI')
export class GameoverUI extends Component {
    @property(Label)
    highLabel: Label = null;
    @property(Label)
    finallLabel: Label = null;
    @property(Label)
    tip:Label=null;
    start() {

    }


    gameoverui(highcount: number, finallcount: number,live:number){
        this.node.active=true;
        this.highLabel.string = ""+highcount.toString();
        this.finallLabel.string = finallcount.toString();
        if(live===0){
            this.tip.string="";
        }else{
            this.tip.string="";
        }
    }


    update(deltaTime: number) {
        
    }
}


