import { _decorator, Component, Node, Prefab, instantiate, Vec3, Animation } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('tx_col')
export class tx_col extends Component {
    @property([Prefab])
    prefabs: Prefab[] = [];

    // 
    txcreator(kind: number, pos: Vec3) {
        if (kind < 1 || kind > this.prefabs.length) {
            console.error('Prefab kind is out of range. It should be between 1 and ' + this.prefabs.length);
            return;
        }

        // 01
        let prefab = this.prefabs[kind - 1];
        if (!prefab) {
            console.error('Prefab at index ' + (kind - 1) + ' is undefined.');
            return;
        }

        // 
        let tx = instantiate(prefab);
        tx.setParent(this.node);
        tx.setWorldPosition(pos);
        let animationComponent = tx.getComponent(Animation);
        animationComponent.on(Animation.EventType.FINISHED, () => {
            tx.destroy();
        }, this);
    }

    update(deltaTime: number) {

    }
}


