import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('pauseui')
export class pauseui extends Component {
    start() {

    }
    hidePauseUI(){
        this.node.active=false;
    }
    showPauseUI(){
        this.node.active=true;
    }
    starshowPauseUI(star:number){
        this.node.active=true;
        let strpnode=this.node.children[0];
        switch(star){
            case 1:
                strpnode.children[1].active = true;
                break;
            case 2:
                strpnode.children[1].active = true;
                strpnode.children[3].active = true;
                break;
            case 3:
                strpnode.children[1].active = true;
                strpnode.children[3].active = true;
                strpnode.children[5].active = true;
                break;
        }

    }

    update(deltaTime: number) {
        
    }
}


