import { _decorator, Component, Node, Label, find, Sprite, Prefab, instantiate, Vec3, Animation, director } from 'cc';
import prefab from '../data/prefab';
import { newcurbeParent } from './newcurbeParent';
import { GameManager } from './GameManager';
const { ccclass, property } = _decorator;

@ccclass('MainGamePanel')
export class MainGamePanel extends Component {
    @property(Label)
    stepCount: Label = null;
    @property(Node)
    barnode: Node = null;
    @property(Prefab)
    starPrefab: Prefab = null;
    starnode1:Node=null;
    starnode2:Node=null;
    progress: number;
    progress_term: number;
    @property(Node)
    termStarnode: Node = null;
    @property(Label)
    l3: Label = null;
    @property()
    progresswidth: number = 550;

    start() {
        this.upstep();
        find('Canvas/Mid/curbParent').on('upstep', this.upstep, this);
        find('Canvas/Mid/curbParent').on('defeat', this.defeat, this);
        find('Canvas/Mid/curbParent').on('2starover', this.star_2over, this);
        find('Canvas/Mid/curbParent').on('uptermstar', this.uptermstar, this);
        find('Canvas/Mid/curbParent').on('initl3', this.initl3, this);
        this.f();
    }
    f() {
        this.starnode2 = instantiate(this.starPrefab);
        this.barnode.addChild(this.starnode2);
        let pos2_x=Math.round((    1-  (newcurbeParent.getInstance().Minstep) /((newcurbeParent.getInstance().Maxstep+1))    )* 10) / 10;
        console.log(pos2_x);
        let pos2 = new Vec3(pos2_x*this.progresswidth, 0, 0);
        this.starnode2.setPosition(pos2);
        this.starnode1 = instantiate(this.starPrefab);
        this.barnode.addChild(this.starnode1);
        let pos1 = new Vec3(1 / (newcurbeParent.getInstance().Maxstep + 1) * this.progresswidth, 0, 0)
        this.starnode1.setPosition(pos1);
    }

    upstep() {
        this.stepCount.string = newcurbeParent.getInstance().step.toString()+"";
        this.progress = (newcurbeParent.getInstance().step + 1) / (newcurbeParent.getInstance().Maxstep + 1)
        this.barnode.getComponent(Sprite).fillRange = this.progress;
    }
    uptermstar() {
        this.progress_term = (newcurbeParent.getInstance().termStarprogress / newcurbeParent.getInstance().totalProgress);
        this.termStarnode.getComponent(Sprite).fillRange = this.progress_term;
    }
    initl3() {
        this.l3.string = newcurbeParent.getInstance().l3;
    }
    defeat() {
        // GameManager.getInstance().gameoverState=true;//
        let animation=this.starnode1.getComponent(Animation);
        if(animation){
            animation.play('progress_star');
            console.log('animation');
        }
        else{
            console.log('animation');
        }
        
        this.barnode.getComponent(Sprite).fillRange = 0;
        //
        animation.on(Animation.EventType.FINISHED, () => {
            // 
            console.log('');
            this.node.emit('defeat-finished');
        });
    }
    star2_over_state:boolean=false;
    star_2over(){
        if(this.star2_over_state) return;
        this.star2_over_state=true;
        let animation=this.starnode2.getComponent(Animation);
        if(animation){
            animation.play('progress_star');
            console.log('animation');
        }
    }

    update(deltaTime: number) {

    }
}


