import { _decorator, Component, Node, tween, Sprite, Vec3, Color } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('newGameManager')
export class newGameManager extends Component {
    //
    private static instance: newGameManager;
    //instance
    public static getInstance(): newGameManager {
        return this.instance;
    }
    @property(Node)
    victroyui: Node = null;
    start() {
        newGameManager.instance = this;
    }
    // public static showDialog(node: Node): Promise<Boolean> {
    //     return new Promise((resolve, reject) => {
    //         if (!node) reject(false);
    //         node.active = true;
    //         // mask
    //         let mask: Node = node.getChildByName("bg");
    //         if (mask) {
    //             mask.opacity = 0;
    //             tween(mask).to(0.3, { opacity: 100 }, { easing: "fade" }).start();
    //         }
    //         // content
    //         let content: Node = node.getChildByName("content");
    //         content.setScale(0, 0);
    //         tween(content)
    //             .to(0.4, { scale: 1 }, { easing: "backOut" })
    //             .call(() => {
    //                 resolve(true);
    //             })
    //             .start();
    //     });
    // }
    showDialog() {
        this.victroyui.active = true;
        // mask
        let mask: Node = this.victroyui.getChildByName("bg");
        let masksp: Sprite = mask.getComponent(Sprite);
        if (masksp) {
            tween(masksp).to(0.3, { color: new Color(masksp.color.r, masksp.color.b, masksp.color.g, 100) }, { easing: "fade" }).start();
        }
        // content
        let content: Node = this.victroyui.getChildByName("content");
        content.scale = new Vec3(0, 0);
        tween(content)
            .to(0.4, { scale: new Vec3(1, 1) }, { easing: "backOut" })
            .call(() => {
            })
            .start();
    }

    


}


