import { _decorator, Component, director, Label, Node, Sprite } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('progress')
export class progress extends Component {
    @property(Node)
    bar:Node=null;
    @property(Label)
    num:Label=null;
    start(){
         director.preloadScene("scene", (completedCount: number, totalCount: number, item: any) => {
        // 
        let progress = completedCount / totalCount;
        
        // 
        let sp=this.bar.getComponent(Sprite);
        sp.fillRange=progress;
        this.num.string=Math.floor((progress*100)).toString()+'%';
    }, () => {
        //  "Main UI" 
        console.log("");
        
        //  "Main UI" 
        director.loadScene("scene");
    });
    }
   
    //  ProgressBar 
// 

//  "Main UI" 
    
   

}


