import { _decorator, Component, Node, Collider2D, IPhysics2DContact, tween, Contact2DType, Sprite, Vec3, find, director } from 'cc';
import { cufbParent } from './cufbParent';
import { fance } from './fance';
import { GameManager } from './GameManager';
import { tx_col } from './texiao/tx_col';
import { Audio } from './Audio';
import { MainGamePanel } from './MainGamePanel';
const { ccclass, property } = _decorator;

@ccclass('curbMove')
export class curbMove extends Component {
    private collider: Collider2D = null;
    public hasAnimated: boolean = false;
    @property()
    score_self: number = 5;

    start() {
        this.collider = this.getComponent(Collider2D);
        if (this.collider) {
            this.collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
        }

    }




    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        if (otherCollider.getComponent(fance)) {
            console.log("");
            if(GameManager.getInstance().timeoutState){
                GameManager.getInstance().addscore(this.score_self);
            }
            if (find('GameManager/audio').getComponent(Audio).audioSource.playing) return;
            find('GameManager/audio').getComponent(Audio).playCollider();
            const sprite = this.node.getComponent(Sprite);
            sprite.enabled = false;
            selfCollider.enabled = false;
            let pos = selfCollider.node.getWorldPosition();
            let pos2 = otherCollider.node.getWorldPosition();
            console.log(pos.x + "pos.x");
            console.log(pos.y + "pos.y");
            console.log(pos.z + "pos.z");
            let kind;
            if (pos2.x > 500 && pos2.x < 800) {
                kind = 1;
                pos = new Vec3(645, pos.y, pos.z);
            }
            else if (pos2.y > 700 && pos2.y < 1200) {
                kind = 2;
                if(GameManager.getInstance().timeoutState){
                    pos = new Vec3(pos.x, 946, pos.z);
                }else{
                    pos=new Vec3(pos.x,930,pos.z);
                }
                
            }
            else if (pos2.x > -100 && pos2.x < 300) {
                kind = 3;
                pos = new Vec3(75, pos.y, pos.z);
            }
            else {
                kind = 4;
                if(GameManager.getInstance().timeoutState){
                    pos = new Vec3(pos.x, 377, pos.z); 
                }
                else{
                    pos=new Vec3(pos.x,360,pos.z);
                }
            }

            find('Canvas/Mid/Fance').getComponent(tx_col).txcreator(1, pos);
            // cufbParent.getInstance().getcstate();
        }
    }


    update(deltaTime: number) {

        // if (cufbParent.getInstance().rotateState) {
        //     tween(this.node)
        //         .to(1, { angle: 90 }, { easing: 'quadInOut' }) // 1
        //         .start();
        // let Angle = this.node.angle;
        // tween(this.node)
        //     .to(1, { angle: Angle + 90 }, { easing: 'quadInOut' }) // 1
        //     .start();
    }
}



