import { _decorator, Animation, Component, director, Label, Node, AudioClip, find, AudioSource, Sprite, Color, tween, Vec3, SpriteFrame } from 'cc';
import { liveUI } from './liveUI';
import { GameoverUI } from './GameoverUI';
import { TimeUI } from './TimeUI';
import { ScoreUI } from './ScoreUI';
import { pauseui } from './pauseui';
import { cufbParent } from './cufbParent';
import { Audio } from './Audio';
import { newcurbeParent } from './newcurbeParent';
import { MainGamePanel } from './MainGamePanel';
import { All } from './all';
import { MapPanel } from './Map/MapPanel';
const { ccclass, property } = _decorator;

@ccclass('GameManager')
export class GameManager extends Component {
    //
    private static instance: GameManager;
    //instance
    public static getInstance(): GameManager {
        return this.instance;
    }
    //
    @property()
    time: number = 60;
    //
    totaltime = 0;
    @property()
    public live: number = 3;
    //
    atimeState: boolean = false;

    // private timer: number = 0; // 
    @property(liveUI)
    liveui: liveUI = null;
    @property(GameoverUI)
    GameoverUI: GameoverUI = null;
    @property(TimeUI)
    TimeUI: TimeUI = null;
    @property(ScoreUI)
    ScoreUI: ScoreUI = null;
    score: number = 0;
    @property(Animation)
    animation: Animation = null;
    @property(Animation)
    animation_add: Animation = null;
    @property(pauseui)
    pauseui: pauseui = null;
    @property(Node)
    //
    countdown: Node = null;
    bg: Node = null;
    //
    @property(Label)
    socre_high: Label = null;
    high_socre: number = 0;

    isAnimationFinished: boolean = false;
    //
    gameoverState = false;
    //
    private remainingTime: number = 10; // 60
    private timer = null; // setTimeoutID
    //
    //
    @property(Animation)
    animation_dlive: Animation = null;
    @property(Node)
    victroyui: Node = null;
    //
    timeoutState: boolean;
    //
    @property(SpriteFrame)
    frame_sp: SpriteFrame = null;


    start() {
        this.timeoutState = false;
        if (!director.getScene().getChildByName('Canvas').getComponent(MainGamePanel)) {
            this.timeoutState = true;
        }
        GameManager.instance = this;
        if (this.timeoutState) {
            this.socre_high.string = this.highest_score().toString();
            this.liveui.liveUIChange(this.live);
            //
            this.animation.on(Animation.EventType.FINISHED, () => {
                this.isAnimationFinished = true;
                this.bg = this.countdown.getChildByName('countdownbg');
                this.bg.active = false;
                // AudioMgr.inst.play(this.bgm);
            });
        }
    }
    onAnimationFinish() {
        // 
        this.isAnimationFinished = true;

        // 
        console.log('Animation finished!');
    }

    //
    addTime_reward() {
        this.time = 20;
        this.TimeUI.TimeUI(this.time);
    }
    //
    alive() {
        this.live = 3;
        this.liveui.liveUIChange(this.live);
    }
    //
    dlive() {
        this.live -= 1;
        if (this.live <= 0) {
            this.live = 0;
        }
        find("GameManager/audio").getComponent(Audio).playDlive();
        this.animation_dlive.play('dliveUI');
        this.liveui.liveUIChange(this.live);
    }
    //-1
    dTime() {
        this.time -= 1;
        if (this.time == 0) {
            this.time = 0;
        }
        this.TimeUI.TimeUI(this.time);
    }

    aTime() {
        this.atimeState = true;
        this.time += 2;

    }
    rewardtype: number = null;
    //
    addscore(s: number) {
        if (this.gameoverState) return;
        if (this.rewardtype === 0) {
            this.score += Math.floor(s / 2);
        } else if (this.rewardtype === 3) {
            this.score += 2 * s;
        } else {
            this.score += s;
        }

        this.ScoreUI.scoreui(this.score);
        // if (find('GameManager/audio').getComponent(Audio).audioSource.playing) return;
        // find('GameManager/audio').getComponent(Audio).playCollider();
    }
    //    
    onPauseButtonClick() {
        // if (!this.pauseButtonState) return;

        // director.pause();

        //
        newcurbeParent.getInstance().changeBState();
        //
        // AudioMgr.inst.pause();
        // this.playercontroal.moveState_no();  //GameManagerplayercontroal
        find("GameManager/audio").getComponent(Audio).playClick();
        // this.pauseui.showPauseUI();
        this.pauseui.starshowPauseUI(newcurbeParent.getInstance().levelStar);
    }

    onPauseButtonClick_Speed() {
        // if (!this.pauseButtonState) return;
        director.pause();
        //
        //
        // AudioMgr.inst.pause();
        // this.playercontroal.moveState_no();  //GameManagerplayercontroal
        find("GameManager/audio").getComponent(Audio).playClick();
        this.pauseui.showPauseUI();
    }
    //
    onReturnmain() {
        find("GameManager/audio").getComponent(Audio).playClick();
        director.resume();
        // this.pauseButtonState = true; //
        // AudioMgr.inst.stop();
        setTimeout(() => {
            director.loadScene("Start_scene");
        }, 500);

    }
    onReturnMap() {
        find("GameManager/audio").getComponent(Audio).playClick();
        director.resume();
        director.loadScene('Map');
    }
    //
    // onAdButtonClick(){
    //     director.pause();
    //     cufbParent.getInstance().changeBState();
    //     this.AdUI.showAdUI();
    // }
    //
    onStartButtonCLick() {
        find("GameManager/audio").getComponent(Audio).playClick();
        director.resume();
        // this.playercontroal.moveState_yes();
        this.pauseui.hidePauseUI();
        // AudioMgr.inst.resume();

    }
    //
    onRestartButtonClick() {
        find("GameManager/audio").getComponent(Audio).playClick();
        // find("Canvas/gameoverUI/Ad").getComponent(TimeOverAd).aliveState = false;
        // find("Canvas/gameoverUI/Ad").getComponent(TimeOverAd).atimeState = false;
        // this.pauseButtonState = true;
        this.onStartButtonCLick();    //
        // AudioMgr.inst.stop();
        setTimeout(() => {
            director.loadScene(director.getScene().name);  //
        }, 500);
    }


    highest_score(): number {
        let hScore = localStorage.getItem('HighestScore');
        let hScoreInt = 0;
        if (hScore !== null) {
            hScoreInt = parseInt(hScore, 10);//10
        }
        if (this.score > hScoreInt) {
            localStorage.setItem("HighestScore", this.score.toString());
        }
        hScore = localStorage.getItem("HighestScore");
        hScoreInt = parseInt(hScore, 10);
        if (hScore == null) hScoreInt = 0;
        return hScoreInt;
    }
    //
    f() {
        find("GameManager/audio").getComponent(Audio).playClick();
        let level = newcurbeParent.getInstance().level;
        let settings = JSON.parse(localStorage.getItem('settings'));
        let s1 = settings[level]['S1'];
        let s2 = settings[level]['S2'];
        let s3 = settings[level]['S3'];
        let l1 = settings[level]['L1'];
        let l2 = settings[level]['L2'];
        let l3 = settings[level]['L3'];
        director.loadScene('Map');
        //currentLevelInfo
        localStorage.setItem('currentLevelInfo', JSON.stringify(settings[level]));
        //
        All.setStateDialog(true);
        setTimeout(() => {
            MapPanel.getInstance().showleveldialog(level + 1, s1, s2, s3, l1, l2, l3);
        }, 500);

    }

    //
    // showDialog() {
    //     this.victroyui.active = true;
    //     // mask
    //     let mask: Node = this.victroyui.getChildByName("bg");
    //     let masksp: Sprite = mask.getComponent(Sprite);
    //     masksp.color = new Color(0, 0, 0, 0);
    //     if (masksp) {
    //         tween(masksp).to(10, { color: new Color(0, 0, 0, 100) }).start();
    //     }
    //     // content
    //     let content: Node = this.victroyui.getChildByName("content");
    //     content.scale = new Vec3(0, 0);
    //     tween(content)
    //         .to(0.4, { scale: new Vec3(1, 1) }, { easing: "backOut" })
    //         .call(() => {
    //         })
    //         .start();
    // }
    playStarAnimation(starNode, callback) {
        //  "animation" 
        let animation = starNode.getComponent(Animation);
        if (animation) {
            // 
            animation.play('star_finaltx');

            // 
            animation.on('finished', () => {
                // 
                if (callback) {
                    callback();
                }
            });
        }
    }
    gameoverStar(star: number) {
        let strpnode = this.victroyui.getChildByName("content").children[4];
        switch (star) {
            case 1:
                console.log('1');
                strpnode.children[1].active = true;
                this.playStarAnimation(strpnode.children[1], () => { });
                break;
            case 2:
                console.log('');
                strpnode.children[1].active = true;
                this.playStarAnimation(strpnode.children[1], () => {
                    strpnode.children[3].active = true;
                    this.playStarAnimation(strpnode.children[3], () => { })
                });
                break;
            case 3:
                console.log('');
                strpnode.children[1].active = true;
                this.playStarAnimation(strpnode.children[1],
                    () => {
                        strpnode.children[3].active = true;
                        this.playStarAnimation(strpnode.children[3],
                            () => {
                                strpnode.children[5].active = true;
                                this.playStarAnimation(strpnode.children[5], () => { })
                            })
                    });
                break;
        }
    }
    showDialog(star: number, level: number, totallevel: number) {
        this.gameoverState = true;
        setTimeout(() => {
            this.victroyui.active = true;
            //if23

            // mask
            // let mask: Node = this.victroyui.getChildByName("bg");
            // let masksp: Sprite = mask.getComponent(Sprite);
            // if (masksp) {
            //     tween(masksp).to(10, { color: new Color(masksp.color.r, masksp.color.g, masksp.color.b, 0) }, { easing: 'linear' }).start();
            // }
            // content
            let content: Node = this.victroyui.getChildByName("content");
            if (star == 0) {
                find("GameManager/audio").getComponent(Audio).playGameover();
                let contentsrpite = content.children[0].getComponent(Sprite);
                contentsrpite.spriteFrame = this.frame_sp;
                this.victroyui.children[2].active = false;//
                content.children[3].active = false;
                content.children[2].setPosition(0, -250, 0);

            } else {
                find("GameManager/audio").getComponent(Audio).playlevelvic();
            }
            if (level == totallevel) {
                content.children[3].active = false;
                content.children[2].setPosition(0, -250, 0);
            }

            content.scale = new Vec3(0, 0, 0);
            tween(content)
                .to(0.4, { scale: new Vec3(1, 1, 1) }, { easing: "backOut" })
                .call(() => { this.gameoverStar(star); })
                .start();
        }, 500);


    }



    //
    gameover() {
        this.gameoverState = true;
        this.high_socre = this.highest_score();
        // let hScore =localStorage.getItem('HighestScore');
        // if(hScore!==null){
        //     this.hScoreInt=parseInt(hScore,10);//10
        // }
        // // let hScoreInt=0;
        // if(this.score>this.hScoreInt){
        //     localStorage.setItem("HighestScore",this.score.toString());
        // }
        // hScore=localStorage.getItem("HighestScore");
        // this.hScoreInt=parseInt(hScore,10);
        find("GameManager/audio").getComponent(Audio).playGameover();
        this.GameoverUI.gameoverui(this.high_socre, this.score, this.live);
        // AudioMgr.inst.stop();
    }

    pausetotal: number = 0;//
    pausetime: number = 0;
    update(deltaTime: number) {
        if (!this.isAnimationFinished) return;//
        if (this.gameoverState) return;
        this.totaltime += deltaTime
        //
        if (this.atimeState) {
            this.TimeUI.TimeUI(this.time);
            this.animation_add.play('animation');
            this.atimeState = false;
        }
        if (this.rewardtype === 4) {
            this.pausetotal += deltaTime;
            if (this.pausetotal >= this.pausetime) {
                this.rewardtype = null;
                this.pausetotal = 0;
            }
            return;
        }

        //60
        if (this.totaltime >= 1 && this.time > 0) {
            this.dTime()
            this.totaltime = 0;
        }
        //
        if (!this.timeoutState) return;
        if (this.time == 0) this.gameover();
    }

}


