import { _decorator, Component, Node, AudioClip, sys, game, AudioSource } from 'cc';
import { AudioMgr } from './AudioMgr';
const { ccclass, property } = _decorator;

@ccclass('BGM')
export class BGM extends Component {
    @property(AudioClip)
    bgMusic: AudioClip = null;
    state_play: boolean = false;
    


    start() {
        const audioSource = AudioMgr.inst.audioSource;
        if (audioSource.playing) return;
        AudioMgr.inst.play(this.bgMusic);

    }


    update(deltaTime: number) {

    }
}


