import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

export enum RewardType {
    half,
    random,
    Two,
    pause

}

@ccclass('reward')
export class reward extends Component {
    @property
    public rewardType: RewardType = RewardType.half;
    @property
    public rewardpausetime=5;
    start() {
        if (this.rewardType === RewardType.random) {
            let rewardType_index = Math.floor(Math.random() * 2);
            // let rewardType_index=3;
            switch (rewardType_index) {
                case 0:
                    this.rewardType = RewardType.Two;
                    console.log("");
                    break;
                case 1:
                    this.rewardType = RewardType.pause;
                    console.log("");
                    break;
            }
        }

    }

    update(deltaTime: number) {

    }
}


