import { _decorator, Component, director, find, instantiate, Node, Prefab, tween, Vec3 } from 'cc';
import { Audio } from '../Audio';
import { newaudio } from '../newaudio';
import { All } from '../all';
const { ccclass, property } = _decorator;

@ccclass('StartUI')
export class StartUI extends Component {
    //
    private static instance: StartUI;
    //instance
    public static getInstance(): StartUI {
        return this.instance;
    }
    pausednode:Node=null;
    @property(Prefab)
    pausePrefab:Prefab
    @property(Prefab)
    loadprefab:Prefab=null;
    loadnode:Node=null;
    start() {
        StartUI.instance = this;
        // Audio.getInstance();
    }


    showspeeddialog(){
        this.pausednode = instantiate(this.pausePrefab);
        this.node.addChild(this.pausednode);

        const pauseuinode = this.pausednode.getChildByName('content');
        // 
        pauseuinode.scale = new Vec3(0, 0, 0);
        tween(pauseuinode)
            .to(0.4, { scale: new Vec3(1, 1, 1) }, { easing: "backOut" })
            .call(() => {
            })
            .start();
    }
    hidespeeddialog() {
        let pauseuinode: Node = this.pausednode.getChildByName('content');
        tween(pauseuinode)
            .to(0.4, { scale: new Vec3(0, 0, 0) }, { easing: 'backIn' }) //  backIn 
            .call(() => {
                // 
                this.pausednode.destroy();
                All.setStateDialog(false);
            })
            .start();
    }

    OnStartButtonClick() {
        console.log('');
        // newaudio.getInstance().playSound('Sound/');
        this.loadnode = instantiate(this.loadprefab);
        this.node.addChild(this.loadnode);
       setTimeout(() => {
            director.loadScene("sceneSpeed",()=>{
                // this.loadnode.destroy();
            });
            
        }, 500);
        }
    OnSecondStartButtonClick() {
        // newaudio.getInstance().playSound('Sound/');
        this.loadnode = instantiate(this.loadprefab);
        this.node.addChild(this.loadnode);
        setTimeout(() => {
            director.loadScene("Map",()=>{
                // this.loadnode.destroy();
            });
            
        }, 500);
        }
    

    update(deltaTime: number) {
        
    }
}


