import { _decorator, Component, Node, instantiate } from 'cc';
import { newcurbeParent } from '../newcurbeParent'; //  newcurbeParent

const { ccclass, property } = _decorator;

@ccclass('PrefabDistributor')
export class PrefabDistributor extends Component {
    @property(Node)
    train: Node = null; // train 

    start() {
        this.distributePrefabs();
    }

    distributePrefabs() {
        if (!this.train) {
            console.error('train ');
            return;
        }

        //  newcurbeParent 
        const parentInstance = newcurbeParent.getInstance();
        if (!parentInstance) {
            console.error(' newcurbeParent ');
            return;
        }

        // 
        const prefabs = [parentInstance.c1, parentInstance.c2, parentInstance.c3, parentInstance.c4];

        //  train 
        const trainChildren = this.train.children;

        //  train 
        for (let i = 0; i < prefabs.length && i < trainChildren.length; i++) {
            const prefab = prefabs[i];
            const childNode = trainChildren[i];

            if (!prefab) {
                console.warn(` ${i + 1} `);
                continue;
            }

            // 
            const instance = instantiate(prefab);

            //  train 
            childNode.addChild(instance);

            // 
            instance.setPosition(0, 0, 0);
        }
    }
}
