import { _decorator, Component, Node, Vec2, Prefab, instantiate, Vec3, tween, Collider2D, Contact2DType, IPhysics2DContact, input, Input, EventTouch, size, Tween, math, find, resources, Sprite, SpriteFrame } from 'cc';
import { fance } from './fance';
import { liveUI } from './liveUI';
import { GameManager } from './GameManager';
import { Audio } from './Audio';
import prefab from '../data/prefab';
import skin from '../data/skin';
import { curbMove } from './curbMove';
const { ccclass, property } = _decorator;

enum Color {
    Red,
    Yellow,
    purple,
    while
}

@ccclass('cufbParent')
export class cufbParent extends Component {

    // private static instance: cufbParent = null;

    // // 
    // public static getInstance(): cufbParent {
    //     if (!this.instance) {
    //         console.error('cufbParent  cufbParent ');
    //     }
    //     return this.instance;
    // }

     //
     private static instance: cufbParent;
     //instance
     public static getInstance(): cufbParent {
         return this.instance;
     }



    private currentRound: number;
    @property
    public blockSize: number = 50;
    @property(Prefab)
    c1: Prefab = null;
    @property(Prefab)
    c2: Prefab = null;
    @property(Prefab)
    c3: Prefab = null;
    @property(Prefab)
    c4: Prefab = null;
    tileMatrix: Node[][] = [];
    //
    colorArray: number[][] = [];
    //
    public isMoving: boolean = false;
    //
    size: number;
    //
    rotateState: boolean = false;
    //
    movesysmol: number;
    //
    state_button: boolean = true;
    //
    cstate: boolean = false;
    //
    @property(liveUI)
    liveui: liveUI = null;
    //
    dstate: boolean = false;
    //
    indexArray: number[] = [];
    posArray: Vec3[] = [];
    pArray: Node[] = [];
    titleMatrix_pos: Vec3[][] = [];
    rewardNode = null;
    //
    reward_time: boolean = false;
    @property(Prefab)
    reward1: Prefab = null;
    @property(Prefab)
    reward2: Prefab = null;
    //
    spriteFramePath = skin;
    //
    prefabPaths = prefab;
    //
    len: number = null;
    //0.7
    nullstate:boolean=false;
    

    start() {
        
        console.log("1");
        this.currentRound = 1;
        this.startNewRound();

    }


    findCoordinates(node) {
        for (let x = 0; x < this.tileMatrix.length; x++) {
            for (let y = 0; y < this.tileMatrix[x].length; y++) {
                if (this.tileMatrix[x][y] === node) {
                    return { x, y };
                }
            }
        }
    }

    PArray() {
        for (let row = 0; row < this.size; row++) {
            for (let col = 0; col < this.size; col++) {
                this.pArray.push(this.tileMatrix[row][col]);
                this.posArray.push(this.titleMatrix_pos[row][col])
            }
        }
    }
    upPArray() {
        this.pArray = [];
        this.posArray = [];
        this.indexArray = [];
    }


    getRandomIndex(array) {
        let index;
        do {
            index = Math.floor(Math.random() * this.size * this.size);
        } while (!array.includes(index));
        return index;
    }

    //
    createreward(): Prefab {
        let type_reward = Math.floor(Math.random() * 2);

        // 
        let rprefab: Prefab = null;
        switch (type_reward) {
            case 0:
                rprefab = this.reward2;
                break;
            case 1:
                rprefab = this.reward1;
                break;
        }
        return rprefab;
    }

    childangle = null;
    f() {
        console.log('f');
        let index = this.getRandomIndex(this.indexArray);
        this.PArray();
        const nod = this.pArray[index]
        const cr = this.findCoordinates(nod);
        // const pos = new Vec3(cr.y * this.blockSize - (this.size / 2) * 40 + 20, cr.x * this.blockSize - (this.size / 2) * 40 + 20, 0)
        const pos = this.posArray[index];
        this.rewardNode = instantiate(this.createreward());
        // reward.setParent(this.node);
        this.node.addChild(this.rewardNode);
        let childNodes = this.node.children; // 
        for (let child of childNodes) {
            this.childangle = child.angle;
            break;
        }
        this.rewardNode.angle = this.childangle;
        this.rewardNode.setPosition(pos);
        // reward.setPosition(pos);
        this.reward_time = true;
        this.tileMatrix[cr.x][cr.y] = this.rewardNode;
        this.colorArray[cr.x][cr.y] = 4;
    }

    countUniqueElementsIn2DArray(arr) {
        // 
        const flattenedArray = arr.flat();
        //  Set 
        const uniqueElements = new Set(flattenedArray);
        return uniqueElements.size;
        // 
    }
    ff(arr) {
        console.log(this.countUniqueElementsIn2DArray(arr)+'');
        console.log(this.len+'len');
        if (this.countUniqueElementsIn2DArray(arr) <this.len+1) {
            console.log('');
            console.log('rewardtime'+this.reward_time);
            if (this.reward_time) return;
            console.log('ff');
            for (let row = 0; row < this.size; row++) {
                for (let col = 0; col < this.size; col++) {
                    if (this.colorArray[row][col] === null) {
                        let node = this.getTileAt(row, col);
                        this.PArray();
                        const index = this.pArray.indexOf(node);
                        this.indexArray.push(index);
                    }
                }
            }
            console.log(this.indexArray + "");
            this.f();
        }
    }

    private animatedNodes: Set<Node> = new Set<Node>();
    moveNode(node: Node, targetWorldposition: Vec3, row: number, col: number) {
        if (this.animatedNodes.has(node)) {
            // 
            return;
        }
        this.isMoving = true; // 
        let pos=node.getWorldPosition();
        this.colorArray[row][col] = null;
        tween(node)
            .to(0.5, { worldPosition: targetWorldposition })
            .call(() => {
                // 
                this.isMoving = false; // 
                // this.countUniqueElementsIn2DArray(this.colorArray);
                this.cstate = false;//falses
                console.log('1111');
                this.animatedNodes.add(node); // 
                if (this.currentRound > 2) {
                    console.log('');
                    this.ff(this.colorArray);
                }
            })
            .start(); // 
        this.cstate = true;
    }
    updateround() {
        this.changeBState();
        this.nullstate=false;
        this.currentRound++;
        this.startNewRound();
        this.reward_time = false;
        if(GameManager.getInstance().rewardtype==4){
            GameManager.getInstance().rewardtype = 4;
        }
        else{
            GameManager.getInstance().rewardtype = null;
        }
       
    }
    updaterotate() {
        let childNodes = this.node.children; // 
        for (let child of childNodes) {
            child.eulerAngles = Vec3.ZERO;
            child.destroy();
        }
        this.node.eulerAngles = Vec3.ZERO;
    }
    startNewRound() {
        // 
        switch (this.currentRound) {
            case 1:
                this.size = 2;
                this.init(this.size);
                break;
            case 2:
                this.updaterotate();
                this.size = 2;
                this.init(this.size);
                break;
            case 3:
                this.updaterotate();
                this.size = 3;
                this.init(this.size);
                break;
            case 4:
                this.updaterotate();
                this.size = 4;
                this.init(this.size);
                break;
            // 
            default:
                this.updaterotate();
                this.size = 5;
                this.init(this.size);
                return;
        }

    }

    ybnum: number = 0
    init(size: number) {
        for (let i = 0; i < size; i++) {
            this.tileMatrix[i] = new Array(size);
            this.colorArray[i] = new Array(size);
            this.titleMatrix_pos[i] = new Array(size);
            for (let j = 0; j < size; j++) {
                // 
                let color = Math.floor(Math.random() * 4);

                // 
                let prefab: Prefab = null;
                switch (color) {
                    case Color.Red:
                        prefab = this.c4;
                        break;
                    case Color.Yellow:
                        prefab = this.c2;
                        break;
                    case Color.purple:
                        prefab = this.c3;
                        break;
                    case Color.while:
                        prefab = this.c1;
                        break;
                }

                // 
                let tile = instantiate(prefab);
                tile.setParent(this.node);
                let pos=new Vec3(j * this.blockSize - (size / 2) * 50 + 25, i * this.blockSize - (size / 2) * 50 + 25, 0);
                tile.setPosition(pos);

                // 
                this.tileMatrix[i][j] = tile;
                this.colorArray[i][j] = color;
                this.titleMatrix_pos[i][j] = pos; 
            }
        }
        this.len = this.countUniqueElementsIn2DArray(this.colorArray);
    }

    

    //
    private touchStartPos: Vec2

    onLoad() {
        // if (!cufbParent.instance) {
        //     cufbParent.instance = this;
        // } else {
        //     console.warn('cufbParent ');
        //     this.destroy();
        // }
        // 
        cufbParent.instance = this;
        input.on(Input.EventType.TOUCH_START, this.onTouchStart, this);
        input.on(Input.EventType.TOUCH_END, this.onTouchEnd, this);
    }

    // 
    onTouchStart(event: EventTouch) {
        console.log("");
        this.touchStartPos = event.getLocation();
    }


    // 
    onTouchEnd(event: EventTouch) {
        console.log("");
        let touchEndPos = event.getLocation();
        let swipeDirection = this.getSwipeDirection(this.touchStartPos, touchEndPos);
        this.handleSwipe(swipeDirection);
    }

    // 
    private getSwipeDirection(startPos: Vec2, endPos: Vec2): string {
        let deltaX = endPos.x - startPos.x;
        let deltaY = endPos.y - startPos.y;

        if (Math.abs(deltaX) > Math.abs(deltaY)) {
            // 
            if (deltaX > 0) {
                return 'right';
            } else if (deltaX < 0) {
                return 'left';
            }
        } else {
            // 
            if (deltaY > 0) {
                return 'up';
            } else if (deltaY < 0) {
                return 'down';
            }
        }

        return 'none'; // 
    }


    // 
    private handleSwipe(direction: string) {
        if (GameManager.getInstance().gameoverState || this.rotateState) return;
        switch (direction) {
            case 'up':
                console.log("up");
                if (!GameManager.getInstance().isAnimationFinished) return;
                console.log('Swipe Up');
                // 
                this.markToRemove('u');
                // this.removeBlocksByColor_u(this.size);
                if (!this.cstate) {
                    GameManager.getInstance().dlive();//ui
                }
                break;
            case 'down':
                if (!GameManager.getInstance().isAnimationFinished) return;
                console.log('Swipe Down');
                // 
                this.markToRemove('d');
                // this.removeBlocksByColor_d(this.size);
                if (!this.cstate) {
                    GameManager.getInstance().dlive();
                }
                break;
            case 'left':
                if (!GameManager.getInstance().isAnimationFinished) return;
                console.log('Swipe Left');
                // 
                this.markToRemove('l');
                // this.removeBlocksByColor_l(this.size);
                if (!this.cstate) {
                    GameManager.getInstance().dlive();
                }
                break;
            case 'right':
                if (!GameManager.getInstance().isAnimationFinished) return;
                console.log('Swipe Right');
                // 
                this.markToRemove('r');
                // this.removeBlocksByColor_r(this.size);
                if (!this.cstate) {
                    GameManager.getInstance().dlive();
                }
                break;
            default:
                break;
        }
    }
    markToRemove(direction: 'r' | 'l' | 'd' | 'u') {
        const size = this.size;
        const isRow = direction === 'r' || direction === 'l';
        const isCol = direction === 'r' || direction === 'u';
        const isContinuousColor = (color: number | null, targetColor: number) => color === targetColor || color === null || color === 4;
        const toRemove: boolean[][] = Array.from({ length: size }, () => Array(size).fill(false));

        let start = direction === 'r' || direction === 'u' ? size - 1 : 0;
        let end = direction === 'r' || direction === 'u' ? 0 : size - 1;
        for (let i = 0; i < size; i++) {
            let consecutiveColorCount = 0;
            let num_null=0;
            for (let j = start; isCol ? j >= end : j <= end; isCol ? j-- : j++) {
                let color = this.colorArray[isRow ? i : j][isRow ? j : i];
                let targetColor = this.getTargetColor(direction);
                if (isContinuousColor(color, targetColor)) {
                    consecutiveColorCount++;
                } else {

                    if (consecutiveColorCount > 0) {
        
                        for (let a = j + (isCol ? 1 : -1); isCol ? a <= j + (consecutiveColorCount) : a >= j - (consecutiveColorCount); isCol ? a++ : a--) {
                            isRow ? toRemove[i][a] = true : toRemove[a][i] = true;
                        }
                        consecutiveColorCount = 0;
                    }
                    break;
                }

                if (consecutiveColorCount > 0 && (j === end)) {
                    for (let a = j; isCol ? a < j + (consecutiveColorCount) : a > j - (consecutiveColorCount); isCol ? a++ : a--) {
                        isRow ? toRemove[i][a] = true : toRemove[a][i] = true;
                    }

                }
            }
        }
        this.moveNodes(toRemove, size, direction);
        if (this.areAllElementsNull(this.colorArray)) {
            if(this.nullstate) return;
            this.nullstate=true;
            this.changeBStatef();
            setTimeout(() => {
                // if (!this.ybState) return;
            // null
            this.updateround();
            GameManager.getInstance().aTime();
            // // console.log("");
            // GameManager.getInstance().addTime();
            this.upPArray();
            // this.ybState=false;

            }, 700);
            
        }
    }
    private getTargetColor(direction: 'r' | 'l' | 'd' | 'u'): number {
        switch (direction) {
            case 'r': return 1;
            case 'l': return 3;
            case 'd': return 0;
            case 'u': return 2;
        }
    }

    fx(){
        
    }

    private moveNodes(toRemove: boolean[][], size: number, direction: 'r' | 'l' | 'd' | 'u'): void {
        for (let row = 0; row < size; row++) {
                for (let col = 0; col < size; col++) {
                    if (toRemove[row][col] === true&&this.colorArray[row][col]!==null) {
                        let node = this.getTileAt(row, col);
                        let pos=node.getWorldPosition()
                        const movePosition = (direction: 'r' | 'l' | 'd' | 'u') => {
                        switch (direction) {
                            case 'r': return new Vec3(pos.x+500,pos.y,pos.z);
                            case 'l': return new Vec3(pos.x-500,pos.y,pos.z);
                            case 'd': return new Vec3(pos.x,pos.y-500,pos.z);
                            case 'u': return new Vec3(pos.x,pos.y+500,pos.z);
                        }
                    };
                        const position=movePosition(direction);

                        this.moveNode(node, position, row, col);
                    }
                }
            }
    }

    changeBState() {
        this.state_button = true;  //
    }
    changeBStatef(){
        this.state_button = false;   //
    }

    getTileAt(x: number, y: number): Node {
        return this.tileMatrix[x][y];
    }

    private currentTween: Tween<Node> = null;
    onrotateButton() {
        if (GameManager.getInstance().gameoverState || this.isMoving) return; //
        if (!GameManager.getInstance().isAnimationFinished) return;//
        find("GameManager/audio").getComponent(Audio).playClick();
        if (!this.state_button) return;
        let newAngle = this.node.angle - 90;
        this.rotateState = true;
        // 
        let parentTween = tween(this.node)
            .to(1, { angle: newAngle }, { easing: 'quadInOut' })
            .call(() => {
                this.rotateState = false; // 
                this.changeBState()
            });

        // 
        let childNodes = this.node.children; // 

        for (let child of childNodes) {

            let childAngle = child.angle + 90;
            // console.log(child.eulerAngles.z);
            // 
            tween(child)
                .to(1, { angle: childAngle }, { easing: 'quadInOut' })
                .start();
        }


        // 
        parentTween.start();
        this.changeBStatef();

        //  tween
        this.currentTween = parentTween;
        //  tween
        this.tileMatrix = this.rotateMatrix90Degrees(this.tileMatrix);
        this.colorArray = this.rotateMatrix90Degrees(this.colorArray);
        this.titleMatrix_pos = this.rotateMatrix90Degrees(this.titleMatrix_pos);
    }

    rotateMatrix90Degrees(matrix: any[][]): any[][] {
        const size = matrix.length;
        const rotatedMatrix = Array.from({ length: size }, () => Array(size).fill(null));

        for (let i = 0; i < size; i++) {
            for (let j = 0; j < size; j++) {
                rotatedMatrix[size - 1 - j][i] = matrix[i][j];
            }
        }

        return rotatedMatrix;
    }
    //null
    areAllElementsNull(twoDimensionalArray: any[][]): boolean {
        return twoDimensionalArray.every(subArray =>
            subArray.every(element => element === null)
        );
    }

    update(deltaTime: number) {
        //
        
        //
        if (this.dstate) return;
        if (GameManager.getInstance().live === 0) {
            console.log('');
            GameManager.getInstance().gameover();
            this.dstate = true;
        }
    }
}

