        console.log('saveStar_final' + this.levelStar);
        const totallevel=JSON.parse(localStorage.getItem('settings')).length;
        console.log(totallevel+'totallevel');
        console.log(this.level+'level');
        GameManager.getInstance().showDialog(this.levelStar,this.level,totallevel);
        localStorage.setItem('settings', JSON.stringify(settings));
