import { _decorator, Component, Node, SpriteFrame, Label, Sprite, director, Input, find } from 'cc';
import { MapPanel } from './MapPanel';
import { All } from '../all';
const { ccclass, property } = _decorator;

@ccclass('level')
export class level extends Component {
    @property(SpriteFrame)
    lockPic: SpriteFrame = null;
    
    @property(SpriteFrame)
    greyStartPic: SpriteFrame = null;

    @property(SpriteFrame)
    yellowStartPic: SpriteFrame = null;
    s1: any;
    s2: any;
    s3: any;
    l1: any;
    l2: any;
    l3: any;

    @property(SpriteFrame)
    completedBackgroundPic: SpriteFrame = null; // New background image for completed levels.

    public settings: any;
    level: number = null;

    start() {
        this.node.on(Input.EventType.TOUCH_END, this.onTouchStart, this);
    }

    initgreyStart() {
        let threeChild = this.node.children[2];
        threeChild.children[0].getComponent(Sprite).spriteFrame = this.greyStartPic;
        threeChild.children[1].getComponent(Sprite).spriteFrame = this.greyStartPic;
        threeChild.children[2].getComponent(Sprite).spriteFrame = this.greyStartPic;
    }

    onePass() {
        let threeChild = this.node.children[2];
        threeChild.children[0].getComponent(Sprite).spriteFrame = this.yellowStartPic;
        threeChild.children[1].getComponent(Sprite).spriteFrame = this.greyStartPic;
        threeChild.children[2].getComponent(Sprite).spriteFrame = this.greyStartPic;
    }

    twoPass() {
        let threeChild = this.node.children[2];
        threeChild.children[0].getComponent(Sprite).spriteFrame = this.yellowStartPic;
        threeChild.children[1].getComponent(Sprite).spriteFrame = this.yellowStartPic;
        threeChild.children[2].getComponent(Sprite).spriteFrame = this.greyStartPic;
    }

    thridPass() {
        let threeChild = this.node.children[2];
        threeChild.children[0].getComponent(Sprite).spriteFrame = this.yellowStartPic;
        threeChild.children[1].getComponent(Sprite).spriteFrame = this.yellowStartPic;
        threeChild.children[2].getComponent(Sprite).spriteFrame = this.yellowStartPic;
    }

    saveMessage(level, s1, s2, s3, l1, l2, l3) {
        this.level = level;
        this.s1 = s1;
        this.s2 = s2;
        this.s3 = s3;
        this.l1 = l1;
        this.l2 = l2;
        this.l3 = l3;
    }
    changePic(levelState: string, num: number, levelStar: number) {
        if (levelState === 'UNLOCKED') {
            // Unlocked: Show number and grey stars
            let childrenArray = this.node.children;
            childrenArray.forEach((childNode) => {
                childNode.active = true;
            });
            this.node.children[0].active = true;
            this.node.children[0].getComponent(Label).string = num.toString();
            this.initgreyStart();
        } else if (levelState === 'PASSED') {
            // Passed: Show number and corresponding stars
            if (levelStar === 1) {
                this.onePass();
            } else if (levelStar === 2) {
                this.twoPass();
            } else if (levelStar === 3) {
                this.thridPass();
            }

            this.node.children[0].active = true;
            this.node.children[0].getComponent(Label).string = num.toString();

            // Update the background image to the completed image
            this.node.getComponent(Sprite).spriteFrame = this.completedBackgroundPic;
        } else if (levelState === 'LOCKED') {
            // Locked: Show lock icon and hide stars
            // this.node.children[1].getComponent(Sprite).spriteFrame = this.lockPic;
            this.node.children[2].active = false;
        }
    }

    onTouchStart() {
        //
        if (this.settings['levelState'] == 'LOCKED') return;
        //
        // if(MapPanel.getInstance().isDialogActive==true) return;
        // if (All.isDialogActive) return;
        // Game.js
        localStorage.setItem('currentLevelInfo', JSON.stringify(this.settings));
        find('Canvas/Camera/Bg/Mask/bg/Map').getComponent(MapPanel).showleveldialog(this.level, this.s1, this.s2, this.s3, this.l1, this.l2, this.l3);
        // director.loadScene('loading');
    }

    update(deltaTime: number) {}
}
