import { _decorator, Component, AudioClip, AudioSource, Button } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('PlaySoundOnClick')
export class PlaySoundOnClick extends Component {
    // 
    @property({ type: AudioClip })
    public clickSound: AudioClip | null = null;

    // AudioSource 
    private audioSource: AudioSource | null = null;

    start() {
        //  AudioSource
        this.audioSource = this.node.addComponent(AudioSource);

        // 
        if (this.clickSound) {
            this.audioSource.clip = this.clickSound;
        }

        // 
        const button = this.node.getComponent(Button);
        if (button) {
            button.node.on(Button.EventType.CLICK, this.playSound, this);
        } else {
            console.warn(' Button ');
        }
    }

    playSound() {
        if (this.audioSource && this.clickSound) {
            this.audioSource.volume = 0.3;
            this.audioSource.play();
        } else {
            console.warn(' AudioSource ');
        }
    }
}
