import { _decorator, Component, Node, Button } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('UIController')
export class UIController extends Component {
    @property(Node)
    targetNode: Node | null = null; // 

    @property(Node)
    showButton: Node | null = null; // 

    @property(Node)
    hideButton: Node | null = null; // 

    start() {
        // 
        if (this.showButton) {
            this.showButton.on(Node.EventType.TOUCH_END, this.showTargetNode, this);
        }
        if (this.hideButton) {
            this.hideButton.on(Node.EventType.TOUCH_END, this.hideTargetNode, this);
        }
    }

    showTargetNode() {
        if (this.targetNode) {
            this.targetNode.active = true; // 
        }
    }

    hideTargetNode() {
        if (this.targetNode) {
            this.targetNode.active = false; // 
        }
    }

    onDestroy() {
        // 
        if (this.showButton) {
            this.showButton.off(Node.EventType.TOUCH_END, this.showTargetNode, this);
        }
        if (this.hideButton) {
            this.hideButton.off(Node.EventType.TOUCH_END, this.hideTargetNode, this);
        }
    }
}
