import { _decorator, Component, Node, Collider2D, Contact2DType, IPhysics2DContact, Sprite, find, Animation } from 'cc';
import { GameManager } from './GameManager';
import { reward, RewardType } from './reward';
const { ccclass, property } = _decorator;

@ccclass('fance')
export class fance extends Component {
    private collider: Collider2D = null;
    start() {
        this.collider = this.getComponent(Collider2D);
        if (this.collider) {
            this.collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
        }
    }

    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        const Reward = otherCollider.getComponent(reward)
        if (Reward) {
            //
            this.onContactReward(Reward)
        }
    }

    onContactReward(Reward: reward) {
        switch (Reward.rewardType) {
            case  RewardType.half:
                GameManager.getInstance().rewardtype=0;
                find('Canvas/Mid/tiptx2').getComponent(Animation).play('tiptx');
                break;
            case RewardType.Two:
                GameManager.getInstance().rewardtype = 3;
                find('Canvas/Mid/tiptx1').getComponent(Animation).play('tiptx');
                break;
            case RewardType.pause:
                GameManager.getInstance().rewardtype = 4;
                GameManager.getInstance().pausetime = Reward.rewardpausetime;
                find('Canvas/Top/timeUI/pausebg').getComponent(Animation).play('pausetx');
        
                break;

        }
        Reward.getComponent(Collider2D).enabled = false;
        Reward.getComponent(Sprite).enabled = false;
    }



    update(deltaTime: number) {

    }
}


