import { _decorator, Component, AudioSource, AudioClip, resources, Node, director } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('newaudio')
export class newaudio extends Component {
    private static _instance: newaudio | null = null;

    private audioSource: AudioSource | null =null;
    private soundCache: Map<string, AudioClip> = new Map();

    private isSoundEnabled: boolean = true; // 
    private isMusicEnabled: boolean = true; // 

    public static getInstance(): newaudio {
        if (!newaudio._instance) {
            const globalNode = new Node('GlobalAudioManager');
            newaudio._instance = globalNode.addComponent(newaudio);

            globalNode.parent = director.getScene()!;
        }
        return newaudio._instance;
    }

    onLoad() {
        if (!this.audioSource) {
            this.audioSource = this.node.addComponent(AudioSource);
        }
    }
    protected start(): void {
        if (!this.audioSource) {
            this.audioSource = this.node.addComponent(AudioSource);
        }
    }

    /**
     * 
     * @param soundPath 
     */
    public playSound(soundPath: string) {
        console.log('playSound1');
        console.log(this.audioSource);
        console.log(this.isMusicEnabled);
        if (!this.isSoundEnabled) return;
        console.log('playSoundw');
        // 
        if (this.soundCache.has(soundPath)) {
            console.log('playSound3');
            this.audioSource.playOneShot(this.soundCache.get(soundPath)!);
            return;
        }

        // 
        resources.load(soundPath, AudioClip, (err, clip) => {
            if (err) {
                console.error(`: ${soundPath}`, err);
                return;
            }
            this.soundCache.set(soundPath, clip);
            this.audioSource!.playOneShot(clip);
        });
    }

    /**
     * 
     * @param musicPath 
     * @param loop 
     */
    public playBackgroundMusic(musicPath: string, loop: boolean = true) {
        if (!this.isMusicEnabled || !this.audioSource) return;

        // 
        if (this.soundCache.has(musicPath)) {
            const clip = this.soundCache.get(musicPath)!;
            this.audioSource.clip = clip;
            this.audioSource.loop = loop;
            this.audioSource.play();
            return;
        }

        // 
        resources.load(musicPath, AudioClip, (err, clip) => {
            if (err) {
                console.error(`: ${musicPath}`, err);
                return;
            }
            this.soundCache.set(musicPath, clip);
            this.audioSource!.clip = clip;
            this.audioSource!.loop = loop;
            this.audioSource!.play();
        });
    }

    /**
     * 
     */
    public stopBackgroundMusic() {
        if (this.audioSource) {
            this.audioSource.stop();
        }
    }

    /**
     * 
     * @param enabled
     */
    public setSoundEnabled(enabled: boolean) {
        this.isSoundEnabled = enabled;
    }

    /**
     * 
     * @param enabled
     */
    public setMusicEnabled(enabled: boolean) {
        this.isMusicEnabled = enabled;
        if (!enabled && this.audioSource) {
            this.audioSource.stop(); // 
        }
    }

    /**
     * 
     */
    public getSoundEnabled(): boolean {
        return this.isSoundEnabled;
    }

    /**
     * 
     */
    public getMusicEnabled(): boolean {
        return this.isMusicEnabled;
    }
}
