import { _decorator, Component, Node, EventTouch, Vec2, UITransform } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('BackgroundScroll')
export class BackgroundScroll extends Component {
    @property(Node)
    background: Node | null = null; // 

    private startTouchPos = new Vec2(); //  Vec2
    private startBackgroundPos = new Vec2(); //  Vec2

    onLoad() {
        // 
        this.node.on(Node.EventType.TOUCH_START, this.onTouchStart, this);
        this.node.on(Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
    }

    onTouchStart(event: EventTouch) {
        // 
        this.startTouchPos = event.getUILocation(); // getUILocation  Vec2
        if (this.background) {
            const bgPos = this.background.position;
            this.startBackgroundPos.set(bgPos.x, bgPos.y); //  Vec2
        }
    }

    onTouchMove(event: EventTouch) {
        if (!this.background) return;

        // 
        const delta = event.getUILocation().subtract(this.startTouchPos);

        // 
        const newPos = new Vec2(
            this.startBackgroundPos.x,
            this.startBackgroundPos.y + delta.y
        );

        // 
        const bgHeight = this.background.getComponent(UITransform)!.height;
        const maskHeight = this.node.getComponent(UITransform)!.height;
        const minY = -(bgHeight / 2 - maskHeight / 2);
        const maxY = bgHeight / 2 - maskHeight / 2;

        newPos.y = Math.max(minY, Math.min(maxY, newPos.y));
        this.background.setPosition(newPos.x, newPos.y); // setPosition  Vec2  x, y
    }
}
