import { _decorator, Component, Node, director } from 'cc';
import { MapPanel } from './Map/MapPanel';
import { StartUI } from './Start/StartUI';
const { ccclass, property } = _decorator;

@ccclass('clickstartgame')
export class clickstartgame extends Component {
    clickstartgame() {
        setTimeout(()=>{
            director.loadScene('loading');
        },500);
        
    }
    clickcancelgame() {
        MapPanel.getInstance().hideleveldialog();
    }
    clickcancelspeed(){
        StartUI.getInstance().hidespeeddialog();
    }
    clickStartspeed(){
        StartUI.getInstance().OnStartButtonClick();
    }
}


