import { _decorator, Component, Node, Button, director } from 'cc';
import { cufbParent } from '../cufbParent';
const { ccclass, property } = _decorator;

@ccclass('BottomAdController')
export class BottomAdController extends Component {

    @property(Node)
    adButton: Node = null;  // Ad 

    @property(Node)
    popupAd: Node = null;   // 

    @property(Node)
    exitButton: Node = null;  // 

    onLoad() {
        // 
        this.popupAd.active = false;

        //  Ad 
        this.adButton.on(Button.EventType.CLICK, this.showPopupAd, this);

        //  Exit 
        this.exitButton.on(Button.EventType.CLICK, this.hidePopupAd, this);
    }

    // 
    showPopupAd() {
        director.pause();
        cufbParent.getInstance().changeBState();
        this.popupAd.active = true;
    }

    // 
    hidePopupAd() {
        director.resume();
        this.popupAd.active = false;
    }
}
