import { _decorator, Component, Node, tween, Vec3 } from 'cc';
import { GlobalPopupState } from './GlobalState';

const { ccclass, property } = _decorator;

@ccclass('PopupController')
export class PopupController extends Component {
    @property(Node)
    popup: Node | null = null; // 

    @property(Node)
    showButton: Node | null = null; // 

    @property(Node)
    hideButton: Node | null = null; // 

    start() {
        const globalState = GlobalPopupState.getInstance();

        if (this.popup) {
            // 
            this.popup.active = globalState.isPopupActive;

            // 
            if (globalState.isPopupActive) {
                this.showPopupAnimation();
            }

            // 
            if (this.showButton) {
                this.showButton.on(Node.EventType.TOUCH_END, this.showPopup, this);
            }

            // 
            if (this.hideButton) {
                this.hideButton.on(Node.EventType.TOUCH_END, this.hidePopup, this);
            }
        }
    }

    showPopup() {
        if (this.popup) {
            this.popup.active = true; // 
            this.showPopupAnimation();
            GlobalPopupState.getInstance().isPopupActive = true; // 
        }
    }

    hidePopup() {
        if (this.popup) {
            this.hidePopupAnimation(() => {
                this.popup.active = false; // 
                GlobalPopupState.getInstance().isPopupActive = false; // 
            });
        }
    }

    private showPopupAnimation() {
        if (this.popup) {
            this.popup.setScale(new Vec3(0, 0, 1)); // 
            tween(this.popup)
                .to(0.3, { scale: new Vec3(1, 1, 1) }) // 
                .start();
        }
    }

    private hidePopupAnimation(callback: () => void) {
        if (this.popup) {
            tween(this.popup)
                .to(0.3, { scale: new Vec3(0, 0, 1) }) // 
                .call(callback) // 
                .start();
        }
    }
}
