import { _decorator, Component, Layers, Node, resources, Size, size, Sprite, SpriteFrame, UITransform, Vec3 } from 'cc';
import skin from '../../data/skin';
const { ccclass, property } = _decorator;

@ccclass('top')
export class top extends Component {
    top:Node=null;
    skintype:number=0;
    spriteFramePath=skin;


    start() {
        this.top=this.node.getChildByName('top-mid');
        for(let i=0;i<4;i++){
            let animal=new Node(`animal${i}`);
            animal.layer=Layers.Enum.UI_2D;
            this.top.addChild(animal);
            let pos=new Vec3(-267+(i*178),-17,0)
            animal.setPosition(pos.x,pos.y,pos.z);
            animal.addComponent(Sprite);
            resources.load(this.spriteFramePath[i + 4 * this.skintype], SpriteFrame, (err: Error, spriteFrame: SpriteFrame) => {
                if (err) {
                    console.error(err);
                    return;
                }
                //sprite
                let sprite=animal.getComponent(Sprite);
                sprite.spriteFrame=spriteFrame;
                const uiTransform = animal.getComponent(UITransform);
                // 
                uiTransform.setContentSize(170, 170);
            });
        }
    }

    update(deltaTime: number) {
        
    }
}


