import { _decorator, Component, Node, Prefab, instantiate, tween, Vec3, Label, director, find } from 'cc';
import { settings } from '../../data/settings';
import { level } from './level';
import { All } from '../all';

const { ccclass, property } = _decorator;

@ccclass('MapPanel')
export class MapPanel extends Component {
    //
    private static instance: MapPanel;
    //instance
    public static getInstance(): MapPanel {
        return this.instance;
    }
    @property(Prefab)
    levelPrefab: Prefab = null;
    @property(Prefab)
    leveldPrefab: Prefab = null;
    
    leveldnode: Node = null;
    

    start() {
        MapPanel.instance = this;
        this.initLevels();
    }

    initLevels() {
        console.log('initLevels');

        const childSlots = this.node.children; //  map 
        if (!localStorage.getItem('settings')) {
            console.log('');
            for (let i = 0; i < settings.length; i++) {
                if (i >= childSlots.length) {
                    console.warn(` ${i + 1} `);
                    break;
                }

                let levelnode = instantiate(this.levelPrefab); // 
                let levelscr = levelnode.getComponent(level);
                if (!levelscr) console.log('levelscr');
                
                levelscr.settings = settings[i]; // 
                levelscr.saveMessage(settings[i]['level'], settings[i]['S1'], settings[i]['S2'], settings[i]['S3'], settings[i]['L1'], settings[i]['L2'], settings[i]['L3']);
                levelscr.changePic(settings[i]['levelState'],(i + 1),settings[i]['levelStar']);

                // 
                childSlots[i].addChild(levelnode);
                levelnode.setPosition(0, 0, 0); // 
            }

            // ()
            localStorage.setItem('settings', JSON.stringify(settings));
        } else {
            console.log('');
            // 
            let newSettings = JSON.parse(localStorage.getItem('settings'));
            for (let i = 0; i < newSettings.length; i++) {
                if (i >= childSlots.length) {
                    console.warn(` ${i + 1} `);
                    break;
                }

                let levelnode = instantiate(this.levelPrefab); // 
                let levelscr = levelnode.getComponent(level);
                if (!levelscr) console.log('levelscr');

                levelscr.settings = newSettings[i]; // 
                levelscr.saveMessage(newSettings[i]['level'], newSettings[i]['S1'], newSettings[i]['S2'], newSettings[i]['S3'], newSettings[i]['L1'], newSettings[i]['L2'], newSettings[i]['L3']);
                levelscr.changePic(
                    newSettings[i]['levelState'],
                    (i + 1),
                    newSettings[i]['levelStar']
                );

                // 
                childSlots[i].addChild(levelnode);
                levelnode.setPosition(0, 0, 0); // 
            }
        }
    }
    // initLevels() {
    //     console.log('initLevels');

    //     const childSlots = this.node.children; //  map 
    //         console.log('');
    //         for (let i = 0; i < settings.length; i++) {
    //             if (i >= childSlots.length) {
    //                 console.warn(` ${i + 1} `);
    //                 break;
    //             }

    //             let levelnode = instantiate(this.levelPrefab); // 
    //             let levelscr = levelnode.getComponent(level);
    //             if (!levelscr) console.log('levelscr');
                
    //             levelscr.settings = settings[i]; // 
    //             levelscr.saveMessage(settings[i]['level'], settings[i]['S1'], settings[i]['S2'], settings[i]['S3'], settings[i]['L1'], settings[i]['L2'], settings[i]['L3']);
    //             levelscr.changePic(settings[i]['levelState'],(i + 1),settings[i]['levelStar']);

    //             // 
    //             childSlots[i].addChild(levelnode);
    //             levelnode.setPosition(0, 0, 0); // 
    //         }

    //         // ()
    //         localStorage.setItem('settings', JSON.stringify(settings));
        
    // }
    
    showleveldialog(stage: number, s1: boolean, s2: boolean, s3: boolean, l1, l2, l3) {
        console.log('');
        console.log(this.leveldPrefab);
        this.leveldnode = instantiate(this.leveldPrefab);
        find('Canvas/Camera/Bg/Mask').addChild(this.leveldnode);

        const contentnode = this.leveldnode.getChildByName('content');
        const titlenode = contentnode.getChildByName('title');
        const starnode = contentnode.getChildByName('star');
        const starChildren = starnode.children;//star
        const startermChildren = starChildren[6].children;//term
        const strNodes = [starChildren[7], starChildren[8], starChildren[9]];

        // 
        [s1, s2, s3].forEach((isActive, index) => {
            if (isActive) {
                strNodes[index].active = true;
            }
        });
        startermChildren[0].getComponent(Label).string = l1;
        startermChildren[1].getComponent(Label).string = l2;
        startermChildren[2].getComponent(Label).string = l3;

        titlenode.getComponent(Label).string = `:${stage}`;

        // 
        contentnode.scale = new Vec3(0, 0, 0);
        tween(contentnode)
            .to(0.4, { scale: new Vec3(1, 1, 1) }, { easing: "backOut" })
            .call(() => {
            })
            .start();
    }

    hideleveldialog() {
        let contentnode: Node = this.leveldnode.getChildByName('content');
        tween(contentnode)
            .to(0.4, { scale: new Vec3(0, 0, 0) }, { easing: 'backIn' }) //  backIn 
            .call(() => {
                // 
                this.leveldnode.destroy();
                All.setStateDialog(false);
            })
            .start();
    }
    returnMain(){
        setTimeout(() => {
            director.loadScene('Start_scene');
        }, 500);
    }

    
}
